<template>
	<v-container>
    <v-layout
      wrap
			justify-center
    >
      <v-flex xs12 md6>
				<h2 class="text-xs-center font-weight-bold pt-1 mb-2 success white--text rounded">NG K GHI DANH</h2>
				<v-card class="rounded-card">
				<v-card-text class="pt-2">
				<div>

				<v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
          <v-text-field
            v-model="name"
            :rules="nameRules"
            label="H tn"
            required
          ></v-text-field>

          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
          ></v-text-field>

					<v-layout row>
					<v-flex xs12 lg6 class="mr-3">
						<v-menu
						ref="menu1"
						v-model="menu1"
						:close-on-content-click="false"
						:nudge-right="40"
						lazy
						transition="scale-transition"
						offset-y
						full-width
						max-width="290px"
						min-width="290px">
							<template v-slot:activator="{ on }">
								<v-text-field 
									v-model="dateFormatted" 
									label="Ngy sinh" 
									persistent-hint 
									prepend-icon="event" 
									@blur="date = parseDate(dateFormatted)" 
									v-on="on" 
									readonly 
									:rules="[v => !!v || 'Nhp ngy sinh']">
								</v-text-field>
							</template>
							<v-date-picker v-model="date" no-title @input="menu1 = false"></v-date-picker>
						</v-menu>
					</v-flex>
					
					<v-select
            v-model="select"
            :items="gender"
            :rules="[v => !!v || 'Chn gii tnh']"
            label="Gii tnh"
            required
					></v-select>
					</v-layout>

					<v-text-field
            v-model="address"
            :rules="addressRules"
            label="a ch"
            required
          ></v-text-field>

					<v-text-field
						label="Mt khu"
						v-model="password"
						min="8"
						:append-icon="e1 ? 'visibility' : 'visibility_off'"
						:append-icon-cb="() => (e1 = !e1)"
						:type="!e1 ? 'password' : 'text'"
						:rules="passwordRules"
						counter
						required
					></v-text-field>
					
					<v-text-field
						label="Nhp li mt khu"
						v-model="re_password"
						min="8"
						:type="!e1 ? 'password' : 'text'"
						:rules="rePasswordRules"
						counter
						required
					></v-text-field>
          
					<v-checkbox
            v-model="checkbox"
            :rules="[v => !!v || 'Ngi phi ng   tip tc!']"
            required
          >
						<div slot='label' style='font-size: 1em'>
							<span>Tho dn  c v ng  vi </span>
							<router-link
								to='/rules'
								tag='span'
								style='cursor: pointer'>
								<span color="primary"><strong><u>th l</u></strong></span>
							</router-link>
							<span> ca cuc thi</span>
						</div>
					</v-checkbox>

          <v-btn
            :disabled="!valid"
            color="success"
            @click="validate"
          >
            Ghi danh 
          </v-btn>
          <v-btn
            color="error"
            @click="reset"
          >
            Hu
          </v-btn>
				</v-form>
			</div>
			</v-card-text>
			</v-card>
			</v-flex>
		</v-layout>
	</v-container>
</template>

<script>
  export default {
    data: vm => ({
      valid: false,
			e1: false,
      name: '',
      nameRules: [
        v => !!v || 'H tn khng c  trng'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail khng c  trng',
        v => /.+@.+/.test(v) || 'a ch E-mail phi hp l'
      ],
			address: '',
			addressRules: [
				v => !!v || 'a ch khng c  trng'
			],
			password: '',
			passwordRules: [
				v => !!v || 'Mt khu khng c  trng'	
			],
			re_password: '',
			rePasswordRules: [
				v => !!v || 'Nhp li mt khu',
				v => !this.password.match(v) || 'Nhp li mt khu khng khp'
			],
      select: null,
      gender: [
        'Nam',
        'N'
      ],
      checkbox: false,
			date: new Date().toISOString().substr(0, 10),
      dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
      menu1: false,
    }),

		computed: {
      computedDateFormatted () {
        return this.formatDate(this.date)
      }
    },

    watch: {
      date () {
        this.dateFormatted = this.formatDate(this.date)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
					this.$router.push('/')
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
			isMobile() {
				if( navigator.userAgent.match(/Android/i)
					|| navigator.userAgent.match(/webOS/i)
					|| navigator.userAgent.match(/iPhone/i)
					|| navigator.userAgent.match(/iPad/i)
					|| navigator.userAgent.match(/iPod/i)
					|| navigator.userAgent.match(/BlackBerry/i)
					|| navigator.userAgent.match(/Windows Phone/i)
				){
					return true;
				}
				else {
					return false;
				}
			},
			formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null

        const [month, day, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      }
    }
  }
</script>
<style>
.rounded-card{
    border-radius:10px;
}
</style>
